#testimonials-container h1 {
	color: #af3434;
}

#scissors-img {
	float: left;
}

.barber-header {
	height: 70px;
	display: flex;
	align-items: center;
}
.barber-header h1 {
	color: #af3434;
}

.barber-services-content ul {
	list-style: disc;
	margin-left: 40px;
}
.barber-services-content li {
	margin: 0;
}

.barber-services-image-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	/* width: 22%; */
}

.barber-services-image-container img {
	height: auto;
	max-width: 100%;
}

@media only screen and (min-width: 800px) {
	#barber-services-container {
		display: flex;
		justify-content: space-between;
	}

	.barber-services-image-container {
		/* max-width: 30%; */
		width: 100%;
		margin-left: 40px;
	}
}
